.card_wrap{
    margin-top: 10px;
    box-shadow: 5px 5px 5px 5px #eee;
    padding: 10px;
    width: 300px;
    height: 300px;
    overflow: hidden;
    border:1px solid #eee;
    box-sizing: border-box;
}

.img_wrap{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    contain: content;

}
.img_wrap img{
    object-fit: contain;
    width: 90%;
    height: 100%;
}
.text_wrap{
    background-color: #fff;
    transition: all ease 0.5s;
    width: 100%;
    height: 100%;
    margin-top: 10px;
    opacity: .5;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.text_wrap.active{
    opacity: 1;
    margin-top: 0;
    border: 1px solid #000;
    transform: translateY(-100%);
}
.text_wrap p{
    text-align: left;
    padding: 10px;
}