

.profile_wrap{
    height: 400px;
    width: 100%;
    margin-top: 30px;
}
.profile_wrap img{
    display: block;
    width: 100%; 
    height: 100%;
    object-fit: cover;    
    border-radius: 40px;

}
.my_info{
    margin-top: 50px;
}
.my_info ul li{
    padding: 5px;
}
.my_info ul li span{
    font-size: 20px;
}
.my_info ul li span:first-child{
    display: inline-block;
    width: 100px;
    text-align: right;
    margin-right: 30px;
    margin-left: -60px;
    
}
.my_info ul li span:last-child{
    display: inline-block;
    text-align: left;
    width: 120px;
    
}