@font-face {
  font-family: "KdamThmorPro-Regular";
  font-weight: normal;
  src: url("./assets/font/KdamThmorPro-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Sunflower-Medium";
  font-weight: normal;
  src: url("./assets/font/Sunflower-Medium.ttf") format("truetype");
}

body {
  margin: 0;
  font-family: "KdamThmorPro-Regular" ,"Sunflower-Medium";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
